:root {
  /* fonts */
  --rem-base: 8; /* used to compute rem value from desired pixel value (e.g., calc(16 / var(--rem-base) * 1rem) = 16px) */
  --body-font-size: 0.5em; /* 8px */
  --body-font-size--desktop: 0.5em; /* 8px */
  --body-font-size--print: 0.5em; /* 8px */
  --body-line-height: 1.5;
  --body-font-color: var(--ds-text-primary);
  --body-font-family: "Roboto Flex", sans-serif;
  --body-font-weight-bold: 600;
  --monospace-font-family: "Roboto Mono", monospace;
  --monospace-font-weight-bold: 500;
  /* base */
  --body-background: var(--ds-background-body);
  --panel-background: var(--ds-background-body);
  --panel-border-color: var(--ds-divider);
  --scrollbar-track-color: var(--ds-background-body);
  --scrollbar-thumb-color: var(--ds-neutral-outlined-border);
  --scrollbar_hover-thumb-color: var(--ds-neutral-outlined-hover-border);
  /* navbar */
  --navbar-background: var(--ds-background-body);
  --navbar-font-color: var(--ds-text-primary);
  --navbar_hover-background: var(--color-black);
  --navbar-button-background: var(--color-white);
  --navbar-button-border-color: var(--ds-divider);
  --navbar-button-font-color: var(--ds-text-primary);
  --navbar-menu-border-color: var(--ds-divider);
  --navbar-menu-background: var(--ds-background-body);
  --navbar-menu-font-color: var(--ds-text-primary);
  --navbar-menu_hover-background: var(--ds-background-body);
  /* nav */
  --nav-background: var(--ds-background-body);
  --nav-border-color: var(--ds-divider);
  --nav-line-height: 1.5;
  --nav-heading-font-color: var(--ds-text-tertiary);
  --nav-muted-color: var(--ds-text-tertiary);
  --nav-panel-divider-color: var(--ds-divider);
  --nav-secondary-background: var(--ds-background-body);
  /* toolbar */
  --toolbar-background: var(--ds-background-body);
  --toolbar-border-color: var(--ds-divider);
  --toolbar-font-color: var(--ds-primary-main);
  --toolbar-muted-color: var(--ds-text-tertiary);
  --page-version-menu-background: var(--ds-background-body);
  --page-version-missing-font-color: var(--ds-text-disabled);
  /* admonitions */
  --caution-color: var(--ds-primary-main);
  --caution-on-color: var(--ds-text-inverse);
  --important-color: var(--ds-failure-main);
  --important-on-color: var(--ds-text-inverse);
  --note-color: var(--ds-cyan-main);
  --note-on-color: var(--ds-text-inverse);
  --tip-color: var(--ds-success-main);
  --tip-on-color: var(--ds-text-inverse);
  --warning-color: var(--ds-warning-main);
  --warning-on-color: var(--ds-text-inverse);
  /* doc */
  --doc-font-color: var(--ds-text-secondary);
  --doc-font-size: inherit;
  --doc-font-size--desktop: calc(16 / var(--rem-base) * 1rem);
  --doc-line-height: 1.5;
  --doc-margin: 0 auto;
  --doc-margin--desktop: 0;
  --heading-font-color: var(--ds-text-primary);
  --heading-font-weight: normal;
  --alt-heading-font-weight: var(--body-font-weight-bold);
  --section-divider-color: var(--ds-divider);
  --link-font-color: var(--ds-primary-main);
  --link_hover-font-color: var(--ds-primary-main);
  --link_unresolved-font-color: var(--important-color);
  --abstract-background: var(--ds-background-level1);
  --abstract-font-color: var(--ds-text-tertiary);
  --abstract-border-color: var(--ds-divider);
  --admonition-background: var(--ds-background-body);
  --admonition-label-font-weight: var(--body-font-weight-bold);
  --caption-font-color: var(--ds-text-primary);
  --caption-font-style: italic;
  --caption-font-weight: var(--body-font-weight-bold);
  --code-background: var(--ds-neutral-soft-bg);
  --code-font-color: var(--ds-text-secondary);
  --example-background: var(--ds-background-body);
  --example-border-color: var(--ds-divider);
  --kbd-background: var(--ds-background-body);
  --kbd-border-color: var(--ds-divider);
  --pre-background: var(--ds-neutral-soft-bg);
  --pre-border-color: var(--panel-border-color);
  --pre-annotation-font-color: var(--ds-text-tertiary);
  --quote-background: var(--ds-background-body);
  --quote-border-color: var(--ds-divider);
  --quote-font-color: var(--ds-text-primary);
  --quote-attribution-font-color: var(--ds-text-tertiary);
  --sidebar-background: var(--ds-background-body);
  --table-border-color: var(--ds-divider);
  --table-stripe-background: var(--ds-background-level1);
  --table-footer-background: var(--ds-background-level1);
  /* toc */
  --toc-font-color: var(--ds-text-secondary);
  --toc-menu-font-weight: var(--body-font-weight-bold);
  --toc-heading-font-color: var(--ds-text-primary);
  --toc-border-color: var(--ds-divider);
  --toc-line-height: 1.5;
  /* footer */
  --footer-line-height: var(--doc-line-height);
  --footer-background: var(--ds-background-body);
  --footer-font-color: var(--ds-text-primary);
  --footer-link-font-color: var(--link-font-color);
  /* dimensions and positioning */
  --site-padding: var(--ds-space-4);
  --site-padding--mobile: var(--ds-space-2);
  --navbar-height: calc(100 / var(--rem-base) * 1rem);
  --toolbar-height: calc(48 / var(--rem-base) * 1rem);
  --drawer-height: var(--toolbar-height);
  --body-top: var(--navbar-height);
  --body-min-height: calc(100vh - var(--body-top));
  --nav-height: calc(var(--body-min-height) - var(--toolbar-height));
  --nav-height--desktop: var(--body-min-height);
  --nav-panel-explore-height: calc(50% + var(--drawer-height));
  --nav-width: calc(300 / var(--rem-base) * 1rem);
  --logo-width: calc(300 / var(--rem-base) * 1rem);
  --toc-top: calc(var(--body-top) + var(--toolbar-height));
  --toc-height: calc(100vh - var(--toc-top) - 2.5rem);
  --toc-width: calc(186 / var(--rem-base) * 1rem);
  --toc-width--widescreen: calc(364 / var(--rem-base) * 1rem);
  --doc-max-width: calc(720 / var(--rem-base) * 1rem);
  --doc-max-width--desktop: calc(828 / var(--rem-base) * 1rem);
  /* stacking */
  --z-index-nav: 10;
  --z-index-toolbar: 20;
  --z-index-page-version-menu: 30;
  --z-index-navbar: 40;
}

@media screen and (max-width: 1023.5px) {
  :root {
    --navbar-height: calc(74 / var(--rem-base) * 1rem);
  }
}
